@use '../../assets/Variables/colors'as *;

@use'../../assets/Variables/elements'  as *;



.ourBlogs{
    z-index: 10;
    position: relative;
    height: fit-content;
    width: 100%;

    @include componentTitle("right");
    .buttomLine{  margin-right: 25%; 
    margin-bottom: 35px;}
    

    .containerBlog{
        width: 90%;
        padding-bottom: 36px;
        .counter{
            width: 57px;
            position: absolute;
            top: 319px;
            left: 6%;
            .counterBtn{
                margin-bottom: 45px;
                font-weight: 400;
                font-size: 40px;
                line-height: 81.02%;
                color:#FFFFFF;
                opacity: 48%;
                background: rgba(0, 0, 0, 0);
                border: 0;
                &:hover{
                    opacity: 100%;
                    cursor: pointer;
                }
                &:focus {
                    color: $red;
                    transition: 0.1s;
                }
            }
        }

        .sectionBlog {
            width:100%;
            margin-left: 22%;
            margin-top: 49px;
            width: 80%;
            
            .dDate{
                .date{
                    font-size: 23px;
                    font-weight: 400;
                    color: #FFFFFF;
                    line-height: 81.02%;
                    
                }
            }
            .dTitle{
        
                position: relative;
                width: 100%;
                margin-bottom: 10px;
                .title{
                    font-size: 78px;
                    font-weight: 400;
                    color: #FFFFFF;
                    line-height: 103.52%;
                    width: 90%;
                    margin-bottom: 10px;
                    margin-top: 10px;
                }
                .dVew{
                    .vew {
                        color: $red;
                        font-weight: 400;
                        font-size: 30px;
                        line-height: 81.02%;
                        background: rgba(0, 0, 0, 0);
                        border: 0;
                        cursor: pointer;
                        &:hover{
                            opacity: 0.7;
                        }
                    }
                }
            }
            .dCard{
                width:100%;
                height: 492px;
                overflow: hidden;
                
                .imgCard{
                    object-fit: cover;
                    border-radius: 10px;
                    width: 100%;
                    margin-top: 5px;
                }
            }
        }
    }

@media screen and (max-width : 982px) {
    .date{
        font-size: 19px !important;   
    }
    .title{
        font-size: 40px !important;
        width: 100% !important;
    }
    .btn-popUp{
        font-size: 20px !important;
       
    }
}

@media screen and (max-width : 770px) {
    .counter{
        top: 219px !important;
    }
}

@media screen and (max-width : 583px) {
    .dCard{
        height: fit-content !important;
    }
    .date{
        font-size: 16px !important;   
    }



    .containerBlog{
        width: 90% !important;
        margin: 0 auto;
    
    }
   
   
    .sectionBlog {
        margin-left: 0 !important;
        width: 100% !important;
      
        .title{
            font-size: 30px !important;
           
        }
        .btn-popUp{
            font-size: 16px !important;
          
        }
    }
    



    .counter{
   
        position: static !important;
        width: 100% !important;
        display: flex !important; 
         margin-top: 20px;
        justify-content: space-between !important;
        
        .counterBtn{

            justify-content: space-between !important;
       
            font-weight: 400;
            font-size:30px !important;
            margin: 0 auto !important;
            line-height: 81.02%;

        }
    }





}



}
