@use '../../assets/Variables/colors' as *;
@use'../../assets/Variables/elements'  as *;


.regumTeamContainer {

    margin-bottom: 20px;
    color: $lightWords;

    h1 {
        margin-left: 60px !important;
    }

    .secondSection {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        margin-top: 0;
        padding-top: 150px;
        background-image: url('../../assets/images/RegumTeam/regumteambg.png');
        background-position: left 161% bottom 46%;
        background-size: 69%;
        background-repeat: no-repeat;


        .regumTeamData {
            width: fit-content;
            padding-bottom: 10px;
            margin-left: 20px;
            border-bottom: 1px solid $lightWords;


            .regumTeamName {
                font-size: 70px;
                font-weight: 700;
                color: $red;
                font-weight: 700;

            }

            .regumTeamPosition {
                margin-top: 10px;
                font-size: 62px;
                font-weight: 100;

            }
        }

    }


    .regumTeamPhotos {
        display: flex;
        width: 50%;

        img {
            height: 300px;
        }
    }


    .regumteamdataContainer {
        align-items: center;
        width: 50%;


        .regumTeamDataHolder {
            height: 90% !important;
        }

        .regumTeamDatasecondHolder {
            height: 60% !important;
            padding-top: 25%;
            padding-bottom: 25%;

        }

    }










    @media (max-width: 1025px) {

        height: fit-content;

        .regumteamdataContainer {
            width: 50% ;
            height: 10% !important;
            margin-left: auto;
            margin-right: auto;


            .regumTeamDatasecondHolder {
                height: 60% !important;
                padding-top: 0% !important;
                padding-bottom: 0% !important;
    
            }

        }


        .regumTeamPhotos {
            width: 80%;
            height: 700px;
            margin-right: auto;
            margin-left: auto;
        }




        .secondSection {
            background-image: none;
            margin-top: 0px;
            flex-direction: column;

            .regumTeamData {

                .regumTeamName {
                    font-size: 50px !important;
                }

                .regumTeamPosition {
                    font-size: 40px !important;
                }

               
            }

        }

  


    }






    @media (max-width: 768px) {



        .secondSection {
            padding-top: 0px;
            .regumTeamData {

                text-align: center;
                padding-top: 0%;
                padding-bottom: 0%;
                margin-left: auto;
                margin-right: auto;


                .regumTeamName {
                    font-size: 40px !important;
                }

                .regumTeamPosition {
                    font-size: 30px !important;
                }


     
            }

 

        }



        h1 {
            font-size: 3rem;
        }


        .regumTeamPhotos {

            width: 80%;
            height: 400px;
            margin-right: auto;
            margin-left: auto;
        }

        .regumteamdataContainer {
            width: 80%;
            height: 10% !important;
            margin-left: auto;
            margin-right: auto;


          
        }
    

    }


    .regumTeamLoadingIcon {

        margin-left: auto;
        margin-right: auto;
        margin-top: 17%;
        margin-bottom: 17%;
    }

}