@use '../../assets/Variables/colors' as *;
@use '../../assets/Variables/elements' as *;

.whatWeDo {
    width: 100%;
    height: fit-content;
    @include  componentTitle(left);
    position: relative;
    padding-bottom: 35px;
    .buttomLine {
        margin-left: 25%;
    }
    .frame {
        display: grid;
        height:fit-content;
        grid-template-columns: auto auto auto;
        // gap: 10px;
        margin-top: 70px;
        row-gap: 72px;
        column-gap: 136px;
        justify-content: center;
        margin-bottom: 72px;
        width: 100%;
        .heroCard {
            padding: 11px, 28px, 9px, 28px;
            background: $bgCard;
            align-items: center;
            width: 372px;
            height: 293px;
            border-radius: 15px;
            column-gap: 136px;

            .logo{
                width: 60px;
                height: 60px;
                padding-top: 41px;
                padding-left: 28px;
                background: rgba(0, 0, 0, 0);
                
            }
            .dTitle {
                width: 281px;
                height: 40px;
                padding-left: 35px;
                background: rgba(0, 0, 0, 0);
                h1 {
                    font-style: normal;
                font-weight: 400;
                font-size: 32px;
                line-height:  81.02%;
                color: $titlesGrey;
                background: rgba(0, 0, 0, 0);
                }
            }
            .dDescription {
                width: 271px;
                height: 95px;
                padding-left: 35px;
                background: rgba(0, 0, 0, 0);
                p {
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 167.52%;
                    color: $lightWords;
                    background: rgba(0, 0, 0, 0);
                }
            }


            &:hover {
                background: $hoverCard;
                
                .logo{
                    width: 60px;
                    height: 60px;
                    padding-top: 11px;
                    padding-left: 28px;
                    background: rgba(0, 0, 0, 0);
                    transition: 0.4s;
                }
                .dTitle {
                    width: 281px;
                    height: 40px;
                    padding-left: 35px;
                    background: rgba(0, 0, 0, 0);
                    transition: 0.4s;
                    h1 {
                        font-style: normal;
                    font-weight: 700;
                    font-size: 32px;
                    line-height:  81.02%;
                    color: $titlesGrey;
                    background: rgba(0, 0, 0, 0);
                    transition: 0.4s;
                    }
                }
                .dDescription {
                    width: 271px;
                    height: 95px;
                    padding-left: 35px;
                    background: rgba(0, 0, 0, 0);
                    transition: 0.4s;
                    p {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 167.52%;
                        color: $lightWords;
                        background: rgba(0, 0, 0, 0);
                        transition: 0.4s;
                    }
                }
            }
        }
        // .heroCard:hover {
            
        // }
    }
    
    @media screen and (max-width: 1470px) {
        .frame {
            column-gap: 70px;
        }
        .heroCard {
            max-width: 300px;
            .logo {
                padding-left: 20px;
            }
            .dTitle {
                padding-left: 20px;
            }
            .dDescription {
                padding-left: 20px;
            }
        }
    }
    @media screen and (max-width: 1340px) {
        .frame {
            column-gap: 50px;
            grid-template-columns:auto auto ;
            .heroCard {
                max-width: 350px;
            }
        }
    }
    @media screen and (max-width: 1280px) {
        .frame {
            column-gap: 50px;
            grid-template-columns:auto auto ;
            justify-content: cen;
            // padding-left: 100px;
            .heroCard {
                max-width: 350px;
            }
        }
    }


    @media screen and (max-width: 889px) {
        .frame {
            grid-template-columns: auto auto;
            justify-content: center;
            // padding-left: 70px;
            
            .heroCard {
                max-width: 300px;
            }
        }
    }

    @media screen and (max-width: 861px) {
        .frame {
            grid-template-columns: auto auto;
            justify-content: center;
            padding-left: 0px;
        }
    }

    @media screen and (max-width: 800px){
        .frame {
            column-gap: 50px;
            grid-template-columns:auto auto ;
            justify-content: center;
            padding-bottom: 20px;
            .heroCard {
                max-width: 350px;
            }
        }
    }

    @media screen and (max-width: 796px){
        .frame {
            grid-template-columns: auto ;
            justify-content: center;
            padding-bottom: 20px;
        }
    }


    @media screen and (max-width: 768px){
        .frame {
            column-gap: 50px;
            grid-template-columns: auto ;
            justify-content: center;
            padding-bottom: 20px;
        }
    }

    @media screen and (max-width: 420px) {
        .frame {
            grid-template-columns: auto ;
            justify-content: center;
            padding-left: 0px;
            .heroCard {
                max-width: 300px;
            }
        }
    }

    @media screen and (max-width: 300px) {
        .frame {
            grid-template-columns: auto ;
            justify-content: center;
            padding-left: 0px;
            .heroCard {
                max-width: 250px;
            }
        }
    }

}
