@use '../../assets/Variables/colors'as *;
@use'../../assets/Variables/elements'  as *;



body.active-popUp {
    overflow: hidden;
    background-color:rgba(0, 0, 0, 0.938);
    // z-index: 5;
}

.btn-popUp {
    background: rgba(0, 0, 0, 0);
    border: 0;
    color: $red;
    font-weight: 400;
    font-size: 30px;
    line-height: 81.02%;
    width: 100%;
    text-align: end;
    cursor: pointer;
        &:hover{
            opacity: 0.7;
        }
}

.overlay {
    background: rgba(49,49,49,0.8);


}
.background{
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.699);
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
}
.popUp-content{
    z-index: 100;
    scrollbar-width: none !important;
	overflow-y: scroll;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: $backgroundPopUp scroll 0 0;
    padding: 14px 28px;
    border-radius: 36px;
    width: 85%;
    height: 90vh;



    .group-data {     
        height: fit-content; 
        margin-top: 123px;
        justify-content: center;
        margin: 0 auto;
        width: 80%;

        .title-date{
            position: relative;
            width: 100%;
            margin-top: 50px;
            .title-popUp {
                color: #FFFFFF;
                font-size: 78px;
                line-height: 103.52%;
                margin-top: 20px;        
            }
            .p-Date {
                color: #FFFFFF;
                font-weight: 400;
                font-size: 23px;
                line-height: 81.02%;   
            }
        }

.containdiv{
margin: 0 auto !important;
width:100% ;
    .imgdiv{
        justify-items: center;
        margin: 47px auto;
      
        overflow: hidden;
        border-radius: 10px;
        img {
           width:100%;
           margin: 0 auto;
           object-fit: cover;
       
            
        }
    }
}

    .d-dec-popUp {
        .dec-popUp {
        color: #FFFFFF;
        font-size: 30px;
        line-height: 167.52%;
        text-align: justify; 
        }
    } 
    }
    &::-webkit-scrollbar { 
        display: none;  
    }  


.close-popUp {
    position: absolute;
    top: 44px;
    right: 57px;
    background: rgba(0, 0, 0, 0);
    border: 0;
    cursor: pointer;
        &:hover{
            opacity: 0.7;
        }
}


@media screen and ( max-width:1000px) {
    .title-popUp {
        font-size: 55px !important;     
    }
            .imgdiv{
                width:100%  !important;
            }    
}


@media screen and ( max-width:675px) {
    .group-data {
        width: 100% !important;
        .title-popUp {
            font-size: 30px !important;
        }
        .p-Date {
            font-size: 14px !important;
        }
        .imgdiv{
            margin: 20px auto !important;
        }

        .d-dec-popUp {
            .dec-popUp {
            font-size: 20px !important;
            }
        }

    }
 
}


@media screen and (max-width:400px) {
    .popUp-content{
        width: 70% !important;
    }
}

}