@use '../../assets/Variables/colors' as *;
@use '../../assets/Variables/elements' as *;

.WhyUs{
    position: relative;
    width: 100%;
    min-height: 100vh;
    align-items: center;
    
    @include componentTitle("right");

    .buttomLine{  margin-right: 25%; 
    margin-bottom: 35px;}

    .container {
        display: flex;
        justify-content: center;
        margin-top:  100px;
        width: 100%;
    }
    main.row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 20px;
    }

    // {==========>col-1<===========}
    .col-1 .contents {
        padding: 0px 30px;
        border-left: 3px solid #D9D9D9;
        margin-left:  118px;

    }
    .col-1 .contents .box {
        position: relative;
        border: none;
        background:  $bgCard-2;
        cursor: pointer;
        transition: all 0.4s;
        margin-bottom: 51px;
        border-radius: 15px;
        max-width: 701px;
        display: flex;
    }
    
    .col-1 .contents .box::before {
        content: '';
        position: absolute;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        right: calc(100% + 19px);
        bottom: calc(50%);
        background-color: #D9D9D9;
        border: 4px solid #141414;
    }
    .col-1 .contents .box:hover::before {
        background-color:$red;
        transition: all 0.4s;
    }
    // {==========>col-2<===========}
    .col-2 .contents {
        padding: 0px 30px;
        border-left: 3px solid #D9D9D9;
    }

    .col-2 {
        margin-top: 730px;
    }
    .col-2 .contents .box {
        max-width: 701px;
        position: relative;
        border: none;
        background:  $bgCard-2;
        cursor: pointer;
        transition: all 0.4s;
        margin-bottom: 51px;
        border-radius: 15px;
        display: flex;
        
    }

    .col-2 .contents .box::before {
        content: '';
        position: absolute;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        right: calc(100% + 19px);
        bottom: calc(50%);
        background-color: #D9D9D9;
        border: 4px solid #141414;
    }
    .col-2 .contents .box:hover::before {
        background-color:$red;
        transition: all 0.4s;
    }
    .box-img {
        height: 96px;
    }
    .box-img img {
        width: 96px;
        height: 96px;
        margin-left: 39px; 
        margin-top: 26px;
    }

    .box-TD h1 {
        position: relative;
        color: #FFFFFF;
        font-weight: 400;
        font-size: 48px;
        line-height: 81.02%;
        margin-top: 45px;
        margin-left: 30px;
    }
    .box .box-TD h1:hover {
        color:$red;
        margin-left: 70px;
        transition: 0.4s;
        
    }
    .box-TD p {
        color: #FFFFFF;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 27px;
        margin-top: 16px;
        margin-left: 30px;
        margin-bottom: 20px;
    }

    .buttomLine {
        margin-top: 109px;
        margin-bottom: 35px;
    }

