@use '../../assets/Variables/colors' as *;
@use'../../assets/Variables/elements'  as *;


.scrolldotts{
    .active{
        a{
            span{
             transform: translateX(0px) ;
             opacity: 1;

            
            }
            &:after{
             background-color: $red !important;
         }
         }
        
       }
    position: fixed;
    height: fit-content;
    top: 40%;
    transform: translateY(-50px) ;
    right: 50px;
    text-align: end;
    z-index: 1000;
    ul{
        list-style: none;
        li{
            cursor: pointer !important;
            // margin-bottom: 10px;
            
            a{
                text-decoration: none;
                color: aliceblue;

                span{
                    display: inline-block;
                    transform: translateX(30px) ;
                    opacity: 0;
                    height: 40px;
                    margin: auto;

           
                }
                &:after {
                    content:'';
                       position: absolute;
                       right: 10;
                       transform: translate(15px,40%);
                       width: 14px ;
                       height: 14px ;
                       border-radius: 50% ;
                       background-color: white ;
                     
               }

           



        }
    




 
            }
           
           li:hover  {
                a{
                   span{
                    transform: translateX(0px) ;
                    opacity: 1;

                   
                   }
                   &:after{
                    background-color: $red !important;
                }
                }
               
             
               }
      
           
       
    }
}