@use '../../assets/Variables/colors' as *;
@use'../../assets/Variables/elements' as *;




.testimonials {

    height: fit-content !important;
    @include componentTitle("left");

    .buttomLine {
        margin-left: 25%;
    }

    .data {
        display: flex;
        margin-top: 147px;


        .left {
            margin-left: 96px;
            width: 400px !important;

            .image {
                border-radius: 50%;
                width: 98px;
                height: 98px;
                overflow: hidden;
                margin-bottom: 30px;

                img {
                    width: 100%;
                    // height: 98px;
                    object-fit: cover;
                }
            }

            h4 {
                width: 100% !important;
                font-weight: 500;
                font-size: 34px;
                line-height: 81.02%;
                color: white;
            }

            .user-title {
                width: 100% !important;
                font-family: 'inter' !important;
                font-weight: 100 !important;
                font-style: normal !important;
                font-size: 35px;
                line-height: 81.02%;
                color: white;
                margin-top: 10px;

            }
        }

        .right {

            padding-left: 150px !important;

            P {
                max-width: 1016px;
                font-weight: 400;
                font-size: 38px;
                line-height: 167.52%;
                color: white !important;
                margin-right: 30px;

                span {
                    color: $red  !important;
                    font-weight: 700;
                    font-size: 38px;
                    line-height: 167.52%;
                }
            }

            .testi-num {
                display: flex;
                font-weight: 200;
                font-size: 33px;
                line-height: 81.02%;
                color: white;
                opacity: 0.62;
                margin-bottom: 30px;
                margin-right: 30px;
                justify-content: flex-end;
            }



            .buttons {
                display: flex;
                cursor: pointer;
                color: white;
                justify-content: flex-end;
                opacity: 0.62;
                margin-bottom: 40px;

                h6 {
                    margin-right: 30px;
                    font-weight: 200;
                    font-size: 33px;
                    line-height: 81.02%;
                }
            }
        }


    }


    @media (max-width: 900px) {
        .data {
            display: block !important;

            .left {

                width: fit-content !important;
                display: flex;
                align-items: center !important;
                margin: 0px !important;
                margin: 0 auto !important;

                .name-title {
                    position: relative;
                    bottom: 10px;

                    h4 {
                        font-size: 20px !important;
                    }

                    h6 {
                        font-size: 20px !important;
                    }
                }

                .image {
                    width: 60px !important;
                    height: 60px !important;
                    margin-right: 30px;
                }

            }

            .right {
                padding: 0 !important;
                margin: 0 20px !important;

                .testi-num {
                    font-size: 16px !important;
                }

                span {
                    font-size: 16px !important;

                }

                p {
                    font-size: 16px;
                    margin: 0 !important;

                }

                .buttons {
                    h6 {
                        font-size: 18px !important;
                    }
                }
            }
        }
    }
}